<template>
    <div class="main">
        <div class="resourceManagement">
            <div class="searchA">
                <div class="add">
                    <el-button size="small" type="primary" icon="el-icon-plus" @click="addBtn(1,0)">新增</el-button>
                </div>
            </div>
            <div class="home_table">
                <el-table :data="tableData" border v-loading="isLoading" element-loading-background="rgba(0, 0, 0, 0.4)" size="medium" row-key="id" :max-height="tableHeight" style="width: 100%" :tree-props="{children: 'children'}">
                    <template v-for="(item, index) in tableHead">
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" :width="item.width" :show-overflow-tooltip="true" v-if="item.prop == 'node_type'">
                            <template slot-scope="scope">
                                <span v-if="scope.row.displayed == 1">菜单资源</span>
                                <span v-else-if="scope.row.displayed == 0">页面资源</span>
                            </template>
                        </el-table-column>
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" :show-overflow-tooltip="true" :width="item.widthS" v-else></el-table-column>
                    </template>
                    <el-table-column prop="address" label="操作" fixed="right" width="220">
                        <template slot-scope="scope">
                            <el-button @click="addBtn(2,scope.row)" type="text" size="small" style="color:#67c23a">新增下级资源</el-button>
                            <el-button @click="editBtn(scope.row)" type="text" size="small">编辑</el-button>
                            <el-button @click="delBtn(scope.row.id)" type="text" size="small" style="color: #ff2b2b">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="pagination">
                    <el-pagination background :current-page.sync="pageNum" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="total, sizes, prev, pager, next" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
                </div>
            </div>
            <!-- 新增编辑弹窗 -->
            <el-dialog :title="title" class="dialogStyle" v-dialogDrag :visible.sync="dialogVisible" width="600px" v-if="dialogVisible" :close-on-click-modal="false">
                <div>
                    <el-form :model="formData" ref="formData" :rules="rule" class="demo-ruleForm">
                        <div class="dialogInput">
                            <div class="padd">
                                <div>
                                    <el-form-item prop="node_name" label="资源名称">
                                        <el-input v-model="formData.node_name" placeholder="请输入资源名称" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                            <div class="padd">
                                <div>
                                    <el-form-item label="资源图标">
                                        <el-input v-model="formData.icon" placeholder="请输入资源图标" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>

                        <div class="dialogInput">
                            <div class="padd">
                                <div>
                                    <el-form-item label="配色设置">
                                        <el-input v-model="formData.color_set" placeholder="请输入配色设置" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>

                            <div class="padd">
                                <div>
                                    <el-form-item label="文件路径">
                                        <el-input v-model="formData.file_path" placeholder="请输入客户端文件路径" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>

                        <div class="dialogInput">
                            <div class="padd">
                                <div>
                                    <el-form-item label="路由路径">
                                        <el-input v-model="formData.router_path" placeholder="请输入路由路径" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>

                            <div class="padd">
                                <div>
                                    <el-form-item label="路由名称">
                                        <el-input v-model="formData.router_name" placeholder="请输入路由名称" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>

                        <div class="dialogInput">
                            <div class="padd">
                                <div>
                                    <el-form-item label="列表排序" prop="order_num">
                                        <el-input v-model="formData.order_num" placeholder="请输入列表排序" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>

                            <div class="padd">
                                <div>
                                    <el-form-item label="模块名称">
                                        <el-input v-model="formData.module_name" placeholder="请输入模块名称" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>

                        <div class="dialogInput">
                            <div class="padd">
                                <div>
                                    <el-form-item label="控制器名">
                                        <el-input v-model="formData.controller_name" placeholder="请输入控制器名" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                            <div class="padd">
                                <div>
                                    <el-form-item label="方法名称">
                                        <el-input v-model="formData.method_name" placeholder="请输入方法名称" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>
                        <div class="dialogInput dialogInput1">
                            <div class="padd">
                                <div>
                                    <el-form-item label="鉴权控制">
                                        <el-switch class="switch" :active-value="1" :inactive-value="0" v-model="formData.authority"></el-switch>
                                    </el-form-item>
                                </div>
                            </div>
                            <div class="padd">
                                <div>
                                    <el-form-item label="需要登录">
                                        <el-switch class="switch" :active-value="1" :inactive-value="0" v-model="formData.signin"></el-switch>
                                    </el-form-item>
                                </div>

                            </div>
                            <div class="padd">
                                <div>
                                    <el-form-item label="菜单显示">
                                        <el-switch class="switch" :active-value="1" :inactive-value="0" v-model="formData.displayed"></el-switch>
                                    </el-form-item>
                                </div>
                            </div>
                            <div class="padd">
                                <div>
                                    <el-form-item label="并列显示">
                                        <el-switch class="switch" :active-value="1" :inactive-value="0" v-model="formData.abreast"></el-switch>
                                    </el-form-item>
                                </div>
                            </div>
                            <div class="padd">
                                <div>
                                    <el-form-item label="默认赋予">
                                        <el-switch class="switch" :active-value="1" :inactive-value="0" v-model="formData.defaulted"></el-switch>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>
                        <div class="dialogInput dialogInput1" style="margin:20px 0">
                            <div class="padd">
                                <div>
                                    <el-form-item label="资源分类">
                                        <el-radio-group v-model="formData.node_type" style="width:200px;">
                                            <el-radio :label="0">电脑端</el-radio>
                                            <el-radio :label="1">手机端</el-radio>
                                        </el-radio-group>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>
                        <div class="dialogInput">
                            <div class="padd">
                                <div class="paddTit">资源说明</div>
                                <div>
                                    <el-form-item>
                                        <el-input v-model="formData.remarks" type="textarea" placeholder="请输入资源说明(100个字符以内)" maxlength='100' :autosize="{ minRows: 2, maxRows: 4}"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>
                        <div style="text-align: center">
                            <el-button type="primary" class="btn" @click="submitForm('formData')">确 定</el-button>
                        </div>
                    </el-form>
                </div>
            </el-dialog>
        </div>
    </div>
</template>
<script>
import {
    resourceManagementListgApi,
    resourceManagementInfoApi,
    resourceManagementDelApi,
    dataDictionaryZdListApi,
    resourceManagementAddApi,
    resourceManagementEditApi,
    hashApi,
} from '@/api/index';

export default {
    data() {
        return {
            total: 0,
            pageNum: 1,
            pageSize: 1000,
            isLoading: false,
            tableHeight: window.innerHeight - 180,
            node_type: 0,
            formData: {
                node_name: '',
                icon: "",
                remarks: "",
                router_path: "",
                file_path: "",
                order_num: "",
                controller_name: "",
                method_name: "",
                module_name: "",
                authority: 1,
                signin: 1,
                displayed: 0,
                router_name: '',
                defaulted: '',
                color_set: '',
                abreast: '',
                node_type: 0,
            },

            method_auth: {},
            editId: '',
            title: '',
            dialogVisible: false,
            rule: {
                node_name: [
                    { required: true, message: '请输入资源名称', trigger: 'blur' },
                ],
                order_num: [
                    { required: true, message: '请输入列表排序', trigger: 'blur' },
                ],

            },
            tableHead: [
                { title: '资源名称', prop: 'node_name' },
                { title: '资源类型', prop: 'node_type' },
                { title: '控制器名', prop: 'controller_name' },
                { title: '方法名称', prop: 'method_name' },


            ],
            tableData: [],
            hashData: '',
            parent_id: "",
        };
    },
    mounted() {
        this.getListData();
    },
    methods: {
        async load(tree, treeNode, resolve) {
            //获取资源列表
            const { data } = await this.axios.post(dataDictionaryZdListApi, {
                curr: 1,
                pagesize: 50,
                id: tree.id
            });
            if (data.code == 1) {
                resolve(data.data.data)
            } else {
                this.$message({ showClose: true, message: data.msg, type: 'error', });
            }
        },

        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.pageNum = 1;
            this.pageSize = newSize;
            this.getListData();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.pageNum = newPage;
            this.getListData();
        },
        //获取Hash
        async getHashData() {
            const res = await this.axios.post(hashApi, { page_hash: 'dataDictionary' });
            if (res.data.code == 1) {
                this.hashData = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error',
                });
            }
        },
        // 新增编辑提交
        submitForm(formName) {
            this.$refs[formName].validate(async (valid) => {
                if (valid) {
                    let datas = {
                        node_type: this.formData.node_type,//资源分类
                        node_name: this.formData.node_name,//资源名称
                        icon: this.formData.icon,//资源图标
                        router_path: this.formData.router_path,//路由路径
                        file_path: this.formData.file_path,//文件路径
                        order_num: this.formData.order_num,//排序
                        controller_name: this.formData.controller_name,//控制器名称
                        method_name: this.formData.method_name,//方法名称
                        module_name: this.formData.module_name,//模块名称
                        authority: this.formData.authority,//鉴权控制
                        displayed: this.formData.displayed,//菜单显示
                        remarks: this.formData.remarks,//备注
                        signin: this.formData.signin,//需要登录
                        abreast: this.formData.abreast,//并列显示
                        color_set: this.formData.color_set,//配色设置
                        defaulted: this.formData.defaulted,//默认赋予
                        router_name: this.formData.router_name,//路由名称
                        hash: this.hashData,
                    };

                    if (this.title.indexOf("新增") != -1) {
                        var urls = resourceManagementAddApi;
                        datas.parent_id = this.parent_id;//
                    } else if (this.title == '编辑') {
                        var urls = resourceManagementEditApi;
                        datas.id = this.editId;
                    }
                    const res = await this.axios.post(urls, datas);
                    if (res.data.code == 1) {
                        this.dialogVisible = false;
                        this.tableData = [];
                        this.$nextTick(() => {
                            this.getListData();
                        });
                    } else {
                        this.getHashData();
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error',
                        });
                    }
                } else {
                    return false;
                }
            });
        },
        //重置对话框表单
        resetForm() {
            this.node_type = 0;
            this.formData = {
                node_name: '',
                icon: "",
                remarks: "",
                router_path: "",
                file_path: "",
                order_num: "",
                controller_name: "",
                method_name: "",
                module_name: "",
                authority: 1,
                signin: 1,
                displayed: 0,
                router_name: '',
                defaulted: '',
                color_set: '',
                abreast: '',
                node_type: 0,
            };
        },
        // 点击新增
        addBtn(e, s) {
            this.dialogVisible = true;
            if (e == 1) {
                this.parent_id = s;
                this.title = '新增父级资源';
            } else {
                this.parent_id = s.id;
                this.title = '新增' + s.node_name + '下级资源';
            }
            this.getHashData();
            this.resetForm(); //重置表单
        },
        // 点击编辑
        editBtn(row) {
            this.title = '编辑';
            this.dialogVisible = true;
            this.getInfo(row.id);
            this.editId = row.id;
            this.getHashData();
            this.$nextTick(() => {
                this.$refs['formData'].clearValidate();
            });
        },
        //编辑详情
        async getInfo(id) {
            const res = await this.axios.post(resourceManagementInfoApi, { id: id });
            if (res.data.code == 1) {
                this.formData = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error',
                })
            }
        },
        //删除
        async delBtn(id) {
            this.$confirm('是否删除该条数据?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
            })
                .then(async () => {
                    const datas = {
                        id: id,
                    };
                    const res = await this.axios.post(resourceManagementDelApi, datas);
                    if (res.data.code == 1) {
                        this.tableData = [];
                        this.$nextTick(() => {
                            this.getListData();
                        });
                        this.$message({ showClose: true, message: '删除成功', type: 'success', });
                    } else {
                        this.$message({ showClose: true, message: res.data.msg, type: 'error', });
                    }
                })
                .catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除',
                    });
                });
        },
        //获取列表
        async getListData() {
            this.isLoading = true;
            let datas = {
                curr: this.pageNum,
                pagesize: 1000,
            };
            const { data } = await this.axios.post(resourceManagementListgApi, datas);
            if (data.code == 1) {
                const menuList = data.data.data; // 后端的菜单数据
                const menuRouters = []
                // 先取出根节点，没有父id的就是根节点
                menuList.forEach((m, i) => {
                    if (m.parent_id === 0) {
                        const module = {
                            router_path: m.router_path,
                            redirect: 'noRedirect',
                            node_name: m.node_name,
                            icon: m.icon,
                            id: m.id,
                            abreast: m.abreast,
                            authority: m.authority,
                            class_name: m.class_name,
                            color_set: m.color_set,
                            controller_name: m.controller_name,
                            defaulted: m.defaulted,
                            displayed: m.displayed,
                            file_path: m.file_path,
                            mainsort: m.mainsort,
                            method_name: m.method_name,
                            module_name: m.module_name,
                            node_name: m.node_name,
                            node_type: m.node_type,
                            order_num: m.order_num,
                            parent_id: m.parent_id,
                            remarks: m.remarks,
                            router_name: m.router_name,
                            router_path: m.router_path,
                            signin: m.signin,
                            meta: { id: m.id, title: m.title, icon: m.icon },
                        }

                        if (chkTree(m.id)) {
                            module.children = [
                                {
                                    path: '',
                                    component: () => import(`@/views/${m.router_path}/index`),
                                    meta: {
                                        title: m.title
                                    }
                                }
                            ]
                            module.children.splice(0, 1)
                        }

                        menuRouters.push(module)
                    }
                })
                // 定义一个递归方法
                function convertTree(routers) {
                    routers.forEach(r => {
                        menuList.forEach((m, i) => {
                            if (m.parent_id && m.parent_id === r.meta.id) {
                                if (!r.children) r.children = []
                                m.fullPath = r.meta.fullPath + '/' + m.path
                                const tmp = r.path + '/' + m.router_path + '/index'
                                const menu = {
                                    component: () => import(`@/views${tmp}`),

                                    router_path: m.router_path,
                                    redirect: 'noRedirect',
                                    node_name: m.node_name,
                                    icon: m.icon,
                                    id: m.id,
                                    abreast: m.abreast,
                                    authority: m.authority,
                                    class_name: m.class_name,
                                    color_set: m.color_set,
                                    controller_name: m.controller_name,
                                    defaulted: m.defaulted,
                                    displayed: m.displayed,
                                    file_path: m.file_path,
                                    mainsort: m.mainsort,
                                    method_name: m.method_name,
                                    module_name: m.module_name,
                                    node_name: m.node_name,
                                    node_type: m.node_type,
                                    order_num: m.order_num,
                                    parent_id: m.parent_id,
                                    remarks: m.remarks,
                                    router_name: m.router_name,
                                    router_path: m.router_path,
                                    signin: m.signin,

                                    meta: { id: m.id, title: m.title }
                                }
                                r.children.push(menu)
                            }
                        })
                        if (r.children) convertTree(r.children)
                    })
                }
                // 检查根节点下是否有子节点
                function chkTree(id) {
                    var tmp = false
                    menuList.forEach((m, i) => {
                        if (m.parent_id && m.parent_id === id) {
                            tmp = true
                        }
                    })
                    return tmp
                }

                convertTree(menuRouters) // 用递归填充
                this.tableData = menuRouters;
                this.isLoading = false;
            } else {
                this.isLoading = false;
                this.$message({ showClose: true, message: data.msg, type: 'error', });
            }
        }
    }
};
</script>
<style >
.resourceManagement .el-table__row--level-1 {
    background: #fdfafa !important ;
}
.resourceManagement .el-table__row--level-2 {
    background: #f5f8fb !important ;
}
.resourceManagement .el-table__row--level-3 {
    background: #fff8f8 !important ;
}
</style>
<style lang="scss" scoped>
.resourceManagement {
    /deep/ .dialogStyle {
        .el-form-item {
            margin-bottom: 20px !important;
        }

        .el-form-item__label {
            line-height: 20px;
        }

        .dialogInput1 {
            .el-form-item {
                margin-bottom: 0px !important;
            }
            .el-form-item__label {
                line-height: 40px;
            }
        }
        .el-form--label-top .el-form-item__label {
            padding: 0;
        }
        .el-dialog__body {
            padding: 30px 15px 30px 25px;
        }
        .el-input-number .el-input__inner {
            text-align: left;
        }
    }
    .searchA {
        display: flex;
        padding: 12px 25px;
        background: #fff;
        .el-select {
            margin-right: 24px;
        }
        .el-input {
            width: auto;
            margin-right: 24px;
        }
        .add {
            flex: 1;
            text-align: right;
        }
        .searchBtn {
            border: solid 1px #2f39ff;
            color: #2f39ff;
        }
    }

    .dialogInput {
        display: flex;
        margin-bottom: 10px;
        .padd {
            flex: 1;
            margin-right: 20px;
            .paddTit {
                margin-bottom: 10px;
            }
        }
        .el-select {
            width: 100%;
        }
    }
    .btn {
        width: 250px;
    }
}
</style>



